
import upload from '../../components/upload.vue';
import Range from 'vux/src/components/range/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
name: 'comment',
components: {
upload,
Range,
Rater,
Group,
Cell,
XButton,
XTextarea
},
data () {
return {
rangeServer: 5,
comment: '',
imgList: [],
pro_id: 0,
shop_id: 0,
order_id: 0,
}
},
methods: {
tijiao: function(){
let self = this
if(this.comment){
self.xarpost('Comment/addComment',{
'img_list': JSON.stringify(self.imgList),
'comment': self.comment,
'range': self.rangeServer,
'shop_id': self.shop_id,
'order_id': self.order_id,
'pro_id': self.pro_id,
'customer_id': self.customer_id,
}).then( res => {
this.showtoast('')
} )
}else{
this.showtoast('')
}
}
},
created: function() {
this.pro_id = this.$route.params.pro_id
this.order_id = this.$route.params.order_id
this.shop_id = this.$route.params.shop_id
this.customer_id = this.$route.params.customer_id



}
}



