export default {
  name: 'Upload',
  props: {
    name: {
        type: String,
        default: ''
    },
    imgList: {
        type: Array,
        default: function(){
            return [
                {
                    localId: 'aaaaa',
                    serverId: 'hhahahah'
                }
            ]
        },
    }
  },
  data () {
    return {
        maxNum: 9,
        test: ''
    }
  },
  methods: {
    choiceImg: function (){
        let wx = this.$wechat
        let self = this
        wx.ready(() => {
            wx.chooseImage({
                count: 9, // 9
                sizeType: ['original', 'compressed'], // 
                sourceType: ['album', 'camera'], // 
                success: function (res) {
                    var localIds = res.localIds; // IDlocalIdimgsrc
                    localIds.map(function(item){
                        wx.uploadImage({
                            localId: item, // IDchooseImage
                            isShowProgressTips: 1, // 1
                            success: function (res) {
                                var serverId = res.serverId; // ID
                                alert(serverId)
                                self.test = serverId
                                self.imgList.push({
                                    localId: item,
                                    serverId: serverId
                                })
                            }
                        });
                    })
                }
            });
        })
    },
    previewImage: function (i) {
        let wx = this.$wechat
        var urls = []
        this.imgList.map(function(item){
            urls.push(item.localId)
        })
        console.log(urls)
        wx.ready(() => {
            wx.previewImage({
                current: i.localId, // http
                urls: urls // http
            });
        })
    }
  }
}



